.header-container {
  display: flex;
  justify-content: center;
  height: 60px;
  background-color: #fff;
}
.header-parent {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  width: 90%;
}
.header-logo {
  font-size: 20px;
  font-family: "Poppins Bold";
  color: #063255;
  
}
.header-options {
  display: flex;
  align-items: center;
  color: #353738;
  font-size: 14px;
  cursor: pointer;
  font-weight: 600;
}
.header-hamburger {
  display: none;
}
.header-option-seperator {
  margin: 0 30px 0 0;
}
.header-sepa{
  margin: 30px;
}
.gitbtn {
  border-radius: 50px;
  padding: 8px 0;
  width: 120px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
  color: #f69825;
  border: 2px solid linen;
  font-size: 12px;
  background-color:#fef3e6;

}

/*IPAD PRO RESPONSIVENESS*/
@media only screen and (max-width: 1110px) {
  header-parent {
    width: 80%;
  }
}
@media only screen and (max-width: 970px) {
  .header-hamburger {
    display: block;
  }
  .header-parent {
    width: 100%;
    padding: 0 40px;
    position: relative;
  }
  .header-options {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: -100%;
    transition: all 0.8s ease-out;
    top: 0px;
    height: 100vh;
    width: 100%;
    justify-content: space-around;
    background-color: #1f2235;
    z-index: 1000;
    opacity: 0.5;
  }
  .header-option-seperator {
    margin: 0;
  }
  .selected-header-option {
    color: var(--white);
  }
  .header-hamburger-bars {
    font-size: 36px;
    cursor: pointer;
    margin: 0px 10px;
    color: var(--white);
  }
  .show-hamburger-options {
    left: 0;
    opacity: 1;
  }
}
